.blog-page {
  max-width: 980px;
  margin: 0 auto;
  padding: 10px;
}

.blog-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 40px;
  /* border-bottom: 1px solid #d2d2d7; */
  padding-bottom: 10px;
}

.blog-header-left {
  font-size: 20px;
  font-weight: bold;
}

.blog-header-right {
  display: flex;
  font-size: 15px;
  gap: 20px;
  align-items: center;
  position: relative;
}

.blog-header-right a {
  text-decoration: none;
  color: #333;
}

.search-link {
  display: flex;
  align-items: center;
}

.search-icon {
  margin-right: 5px;
}

.blog-post {
  position: relative;
  margin-bottom: 30px;
  border-radius: 30px;
  overflow: hidden;
  aspect-ratio: 2 / 1;
}

.blog-post img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.blog-post-content {
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  padding: 30px;
  background: linear-gradient(to top, rgba(0,0,0,0.8) 0%, rgba(0,0,0,0.4) 50%, rgba(0,0,0,0) 100%);
  color: white;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  text-align: left;
}

.blog-post-tag {
  font-size: 10px;
  text-transform: uppercase;
  margin-bottom: 3px;
  text-align: left;
  font-weight: bold;
}

.blog-post-content h2 {
  font-size: 24px;
  margin: 0 0 3px 0;
  font-weight: bold;
  text-shadow: 1px 1px 3px rgba(0,0,0,0.5);
  text-align: left;
}

.blog-post-content p {
  font-size: 12px;
  margin: 0;
  text-align: left;
  font-weight: bold;
}

.blog-page.blur {
  filter: blur(5px);
  transition: filter 0.3s ease-in-out;
}

.blog-content.blur {
  filter: blur(5px);
  transition: filter 0.3s ease-in-out;
}

@media (max-width: 768px) {
  .blog-header {
    flex-direction: column;
    align-items: flex-start;
  }

  .blog-header-right {
    margin-top: 10px;
    width: 100%;
    justify-content: space-between;
  }
}

.blog-content h1 {
  text-align: left;
  margin-bottom: 30px;
}



.blog-post-content h2 {
  font-size: 32px;
  margin-bottom: 10px;
}

.blog-post-content p {
  font-size: 16px;
}
